import React from 'react'
import PropTypes from 'prop-types'
import Card from 'react-bootstrap/Card'
import { navigate } from 'gatsby'
import _ from 'lodash'
import usePosts from '../hooks/usePosts'

const SeriesCard = ({ series, seriesTotalNo }) => {
  const { posts } = usePosts()

  const lastDate = _(posts)
    .filter(post => post.node.frontmatter.series === series)
    .mapValues(post => post.node.frontmatter.date)
    .sortBy(date => date).last()

  const seriesSlug = `/series/${_.kebabCase(series)}`

  return (
    <Card>
      <Card.Body>
        <p className={'title h4 text-pink pointer'} onClick={() => navigate(seriesSlug)}>
          {series}
        </p>
        <p>
          {seriesTotalNo}개의 포스트 - 마지막 변경일 {lastDate}
        </p>
      </Card.Body>
    </Card>
  )
}

SeriesCard.propTypes = {
  series: PropTypes.string.isRequired,
  seriesTotalNo: PropTypes.number.isRequired,
}

export default SeriesCard