import React from 'react'
import { graphql } from 'gatsby'
import Sidebar from '../components/Sidebar'
import { Container, Row } from 'react-bootstrap'
import Paging from '../components/Paging'
import Content from '../components/Content'
import SeriesCard from '../components/SeriesCard'

const SeriesListTemplate = ({ data, pageContext }) => {

  const {
    totalPageNo,
    currentPageNo,
    hasPrevPage,
    hasNextPage,
    prevPagePath,
    nextPagePath,
    tag
  } = pageContext

  const { edges } = data.allMarkdownRemark

  return (
    <Container>
      <Row>
        <Sidebar selected={'series'}/>
        <Content>
          {
            edges.map(({ node }) => {
              const { series, seriesTotalNo } = node.frontmatter
              return (
                <SeriesCard series={series}
                            seriesTotalNo={seriesTotalNo}
                />
              )
            })
          }
          <Paging totalPageNo={totalPageNo}
                  currentPageNo={currentPageNo}
                  prevPagePath={prevPagePath}
                  nextPagePath={nextPagePath}
                  hasPrevPage={hasPrevPage}
                  hasNextPage={hasNextPage}
                  slug={'/series'}
          />
        </Content>
      </Row>
    </Container>
  )
}

export const query = graphql`
  query SeriesListTemplate($pageLimit: Int!, $pageOffset: Int!) {
    allMarkdownRemark(
        limit: $pageLimit,
        skip: $pageOffset,
          filter: {frontmatter: {template: {eq: "post"}, publish: {eq: "Yes"}, seriesNo: {eq: 1}}}, 
          sort: {fields: frontmatter___series}
      ){
      edges {
        node {
          frontmatter {
            series
            seriesTotalNo
          }
        }
      }
    }
  }
`

export default SeriesListTemplate
